.default-works {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 120px);
    background-color: #000;
    background-image: url(../../assets/bg1.jpg);
    background-size: cover;
    background-position: bottom;
    overflow-y: auto;
}
.default-works .section-title {
    font-size: 30px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-weight: 600;
    padding-top: 80px;
    padding-bottom: 30px;
}

.default-works .section-desc {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: #ffb340;
    margin: 10px 0 30px 0;
    text-shadow: 1px 1px 2px #999;
}
.default-works .section-body {
    height: 70%;
    margin: 0;
}
.section-works-area {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.6);
    overflow-y: auto;
}
.project-item {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0px;
    text-align: center;
    margin: 30px auto 30px auto;
}
.project-item a {
    display:flex;
    flex-direction: column;
    align-items:center;
    text-decoration: none;
    padding-bottom: 30px;
}
.project-item--img {
    width:190px;
    height:150px;
    background-color:#EEE;
    border-radius:30px;
    box-shadow:0px 10px 50px rgba(0, 0, 0, 0.2);
}
.project-item--img:hover {
    box-shadow:0px 10px 50px rgba(255, 179, 64, 0.6);
    transition: 0.3s;
}
.project-item--img img {
    width:100%;
    height:100%;
    border-radius:30px;
    border: 2px solid #ffb340;
    opacity: 0.8;
}
.project-item--img img:hover {
    transition: 0.2s;
    opacity: 1;
}
.project-item--title {
    font-size:18px;
    font-weight: bold;
    color:#FFF;
    padding: 5px;
}
.project-item--button {
    display: inline-block;
    background-color: #ffb340;
    color: #333;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 600;
    text-decoration: none;
    padding: 10px 30px;
    margin-top: 5px;
    border-radius: 20px;
    opacity: .8;
}
.project-item--button:hover {
    opacity: 1;
}

@media (max-width:800px) {
    .project-item {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width:650px) {
    .project-item {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width:450px) {
    .project-item {
        grid-template-columns: repeat(1, 1fr);
    }
}